import helper from "../../../common/helper.js";
export default {
	data() {
		return {
			originalData: [],
			dangerCheckBox: [],
			showDangerCheckBox: false,
			current: -1,
			choose: '',
			dangerRoad: '',
			date: '请选择月份',
			rollId: null
		}
	},
	methods: {
		DateChange(e) {
			this.date = e.detail.value
		},
		reset() {
			this.dangerCheckBox = this.originalData;
			this.choose = '';
			this.current = -1;
			this.dangerRoad = ';'
		},
		showMoadel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		getInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_AllBZ',
				data: {
					DWCD: self.$store.state.userInfo.DWCD
				},
				method: 'GET',
				header: {
					'content-type': 'application/json' // 默认值
				},
				success(res) {
		
					let data = JSON.parse(res.data)
					if (res.data.length == 0) {
						self.showMoadel('此处没有隐患源')
					} else {
						self.originalData = data;
						self.dangerCheckBox = data;
					}
				}
			})
		},
		radioChange: function(evt) {
			for (let i = 0; i < this.dangerCheckBox.length; i++) {
				if (this.dangerCheckBox[i].XZCD === evt.target.value) {
					this.current = i;
					this.choose = evt.target.value;
					break;
				}
			}
			console.log(this.choose)
		},
		copy(path) {
			uni.setClipboardData({
				data: this.$store.state.urlPrefix + path,
				success: function() {
					console.log('success');
				}
			});
			uni.getClipboardData({
				success: function(res) {
					console.log(res.data);
				}
			});
		},
		upload(type) {
			var self = this;
			
			if (self.rollId == 6) {
				if (!self.choose) {
					self.showMoadel('请选择工点！')
				} else if (this.date == '请选择月份') {
					self.showMoadel('请选择月份!')
				}else {
					uni.showLoading({
						title:'下载中。。。',
						mask:true
					})
					uni.request({
						url: self.$store.state.urlPrefix + '/Home/Get_PointsInfoByMonth',
						data:{
							xzcd: self.choose,
							time: self.date
						}
					}).then(data => {
						var [error,res] = data;
						uni.hideLoading();
						console.log(res.data,typeof(res.data))
						if (res.data.code == 0) {
							
							if (type == 'copy') {
								self.copy(res.data.data[0])
							} else {
								self.zzy(res.data.data[0])
							}
						} else {
							if (type == 'copy') {
								self.showMoadel('复制失败')
							} else {
								self.showMoadel('下载失败')
							}
						}
					})
				}
			} else {
				uni.request({									
					url: self.$store.state.urlPrefix + '/Home/Get_PointsInfoByMonthBySelf',
					data:{
						ryxh: self.$store.state.userInfo.RYXH,
						time: self.date
					}
				}).then(data => {
					var [error,res] = data;
					uni.hideLoading();
					console.log(res.data,typeof(res.data))
					if (res.data.code == 0) {
						self.zzy(res.data.data[0])
					} else {
						self.showMoadel('下载失败')
					}
				})
			}
		},
		zzy(path) {
			uni.downloadFile({
				url: this.$store.state.urlPrefix + path,
				success: res => {
					if (res.statusCode === 200) {
						// console.log(res.tempFilePath);
						var tempFilePath = res.tempFilePath;
		
						uni.openDocument({
							filePath: tempFilePath,
							success() {
		
							}
						})
					}
				}
			});
		}
	},
	mounted() {
		this.rollId = this.$store.state.userInfo.ROLEID;
		this.getInfo();
		// this.date = helper.nowMon();
	}
}
